import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { SalesProgramComponent } from './sales-program.component';
const routes: Routes = [
  {
    path: '',
    component: SalesProgramComponent
  },
  {
    path: 'promotions',
    loadChildren: () => import('./promotions/promotions.module').then(m => m.PromotionsModule)
  },
  {
    path: 'discount-customer',
    loadChildren: () => import('./discount-customer/discount-customer.module').then(m => m.DiscountModule)
  },
  {
    path: 'discount-employee',
    loadChildren: () => import('./discount-employee/discount-employee.module').then(m => m.DiscountEmployeeModule)
  },
  {
    path: 'reward-points',
    loadChildren: () => import('./reward-points/reward-points.module').then(m => m.RewardPointsModule)
  },

  {
    path: 'coupons',
    loadChildren: () => import('./coupons/coupons.module').then(m => m.CouponsModule)
  },
  {
    path: 'voucher',
    loadChildren: () => import('./voucher/voucher.module').then(m => m.VoucherModule)
  },
 
  {
    path: 'card-goods',
    loadChildren: () => import('./card-goods/card-goods.module').then(m => m.CardGoodsModule)
  },
]

@NgModule({
  declarations: [SalesProgramComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class SalesProgramModule { }
